body{
  margin:0;
}
ul{list-style: none; display: flex; text-align: center;}
li{margin:0px 10px 0px 0px;}
a{text-decoration: none; cursor: help;}

.movie{
      width: 240px;
      height: auto;

      column-width:200px;
      transition: all 2s;
      display: inline;
    }
    .movie figure{
      display: block;
      border:1px solid rgba(0,0,0,0.2);
      border-radius: 15px;
      margin:0px 20px 20px 20px;
      padding:10px;
      box-shadow: 2px 2px 5px rgba(0,0,0,0.5);;
      background-color: #d1d1d1;
      text-align: center;
      transition: all 2s;
    }
    .movie figure img{
      width:100%;
      border-radius: 25px;

    }
.movies-title , .movies-rating {border-bottom: 1.35px solid; padding:10px 0px 10px 0px; margin:0;}
.movies-title{ height: 40px; font-size: 15px; font-family:inherit;}
.genres{margin:10px 0px 10px 0px; padding:0;  justify-content: center; font-weight: 500;}
.redcap{ width: 150px;height: 40px; background-color: red; display:inline-block; }
